// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';

import VueLazyLoad from 'vue-lazyload';
require("expose-loader?/jquery/dist/jquery.min.js");
require("expose-loader?$!jquery");


Vue.use(VueLazyLoad,{
  error:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1521346450968&di=c9301f20572bb58ce21a49fc2c3ac75d&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimgad%2Fpic%2Fitem%2Fb21c8701a18b87d69f0ccc220c0828381f30fd45.jpg',
  loading:'data:image/gif;base64,',
})


Vue.config.productionTip = false;

/* eslint-disabimport new */
new Vue({
  el: '#app',
  router,
  render:h=>h(App),
});



